import { FC, useState, useEffect } from "react";
import { ValueType, OptionTypeBase } from "react-select";
import classnames from "classnames/bind";

import { Language, Theme, WINDOW_SIZE } from "common/constants/enum";
import { getOptGroupByOptions } from "common/utils/getOptGroupByOptions";
import { getFilteredCities } from "common/utils/getFilteredCities";
import {
    Heading,
    HeadingType,
    HeadingTag,
} from "common/components/new/Heading";
import { ModalSheet } from "common/components/new/ModalSheet";
import { Icon } from "common/components/Icons";
import SearchIcon from "common/components/Icons/icons/24/search.svg";
import DownIcon from "common/components/Icons/icons/24/down.svg";
import { Text, TextTag } from "common/components/Text";
import { useWindowDimensions } from "common/hooks/useWindowDimensions";

import { SelectWithClinics } from "./components/SelectWithClinics";
import { filterOption } from "./helpers/filterOption";
import { getAddressByCinic } from "./helpers/getAddressByClinic";
import {
    SelectWithSearchProps,
    OptionProps,
    FilterOption,
} from "./SelectWithSearch.types";
import styles from "./SelectWithSearch.module.scss";
import { clearValueSelect } from "./helpers/cleatValueSelect";

const cx = classnames.bind(styles);

export const SelectWithSearch: FC<SelectWithSearchProps> = ({
    options,
    className,
    errorText,
    placeholder,
    name,
    value,
    language = Language.SPAIN,
    onChange,
    customSortOptions,
    theme = Theme.DARK,
    title,
    submitRef,
    clinics,
    isShowClinics,
    selectedClinic,
    onChangeClinic,
}) => {
    const [isMenuOpen, setIsMenuOpen] = useState(false);
    const [isOpenModal, setOpenModal] = useState(false);
    const [inputValue, setInputValue] = useState("");
    const [fuzzyOptions, setFuzzyOptions] = useState(options);
    const { width } = useWindowDimensions();
    const isMobileView = width <= WINDOW_SIZE.DESKTOP;

    const optionsList = Object.values(getOptGroupByOptions(options));

    let optionsListCustomSorted;

    if (customSortOptions) {
        optionsListCustomSorted = customSortOptions(optionsList);
    }

    const handleChange = (option: ValueType<OptionTypeBase, false>) => {
        if (option) {
            onChange(option as OptionProps);
        }
    };

    const handleInputChange = (newValue: string) => {
        setFuzzyOptions(
            newValue
                ? ((getFilteredCities<OptionProps>(newValue, options, [
                      "label",
                  ]) as unknown) as OptionProps[])
                : options
        );

        setInputValue(newValue);
    };

    const handleClearInputValue = () => {
        setInputValue("");
        onChange({ value: "", label: "" });
        clearValueSelect(placeholder || "");
    };

    const filterOptionsWithSmart = (props: FilterOption, inputValue: string) =>
        filterOption(
            props,
            inputValue,
            fuzzyOptions.map((opt) => opt.label)
        );

    const toggleOpenModal = () => {
        setOpenModal((prevState) => !prevState);

        if (!isShowClinics) {
            callbackMenuOpen();
        }
    };

    const callbackMenuOpen = () => {
        setIsMenuOpen(true);
    };

    const callbackMenuClose = () => {
        setIsMenuOpen(false);
    };

    useEffect(() => {
        if (isOpenModal && isMobileView) {
            document.body.style.touchAction = "none";
            document.body.style.overflowY = "hidden";

            if (submitRef?.current) {
                submitRef.current.style.opacity = "0";
            }
        } else {
            document.body.style.touchAction = "pan-y";
            document.body.style.overflowY = "auto";

            if (submitRef?.current) {
                submitRef.current.style.opacity = "1";
            }
        }
    }, [isOpenModal, isMobileView]);

    useEffect(() => {
        if (isMobileView) {
            callbackMenuOpen();
        }
    }, []);

    useEffect(() => {
        if (!value) {
            return;
        }

        if (!isShowClinics && isOpenModal) {
            toggleOpenModal();
        }
    }, [value]);

    return (
        <div className={cx("root", className, `theme_${theme}`)}>
            <button
                type="button"
                onClick={toggleOpenModal}
                className={cx("button-select-sities", {
                    "button-select-sities_error": errorText,
                    "button-select-sities_has-value": Boolean(value),
                })}
            >
                <Icon className={cx("icon-search")}>
                    <SearchIcon />
                </Icon>
                <Icon
                    className={cx("icon-arrow", {
                        "icon-arrow_active": !isMenuOpen,
                    })}
                >
                    <DownIcon />
                </Icon>

                <Text tag={TextTag.Span}>{value?.label || placeholder}</Text>
                {value && placeholder && (
                    <label
                        className={cx("placeholder", {
                            placeholder_light: theme === Theme.LIGHT,
                            "placeholder_has-value": Boolean(value),
                        })}
                    >
                        {placeholder}
                    </label>
                )}
            </button>

            {isMobileView && (
                <ModalSheet
                    isOpen={isOpenModal}
                    language={language}
                    hasValue={Boolean(value)}
                    onClose={toggleOpenModal}
                >
                    <Heading
                        className={cx("modal-title")}
                        tag={HeadingTag.H2}
                        type={HeadingType.M}
                    >
                        {title}
                    </Heading>
                    <SelectWithClinics
                        instanceId={name}
                        isMenuOpen={isMenuOpen}
                        isMobileView={isMobileView}
                        value={value}
                        clinics={clinics}
                        selectedClinic={selectedClinic}
                        isOpenModal={isOpenModal}
                        theme={theme}
                        language={language}
                        placeholder={placeholder}
                        onChangeClinic={onChangeClinic}
                        isShowClinics={isShowClinics}
                        autoFocus={true}
                        options={optionsListCustomSorted || optionsList}
                        className={cx("only-mobile")}
                        inputValue={inputValue}
                        filterOption={filterOptionsWithSmart}
                        onMenuOpen={callbackMenuOpen}
                        onMenuClose={callbackMenuClose}
                        handleChange={handleChange}
                        handleInputChange={handleInputChange}
                        handleClearInputValue={handleClearInputValue}
                    />
                </ModalSheet>
            )}

            <SelectWithClinics
                instanceId={name}
                isMenuOpen={isMenuOpen}
                value={value}
                language={language}
                clinics={clinics}
                placeholder={placeholder}
                selectedClinic={selectedClinic}
                onChangeClinic={onChangeClinic}
                options={optionsListCustomSorted || optionsList}
                theme={theme}
                isShowClinics={isShowClinics}
                className={cx("only-desktop")}
                inputValue={inputValue}
                filterOption={filterOptionsWithSmart}
                handleClearInputValue={handleClearInputValue}
                onMenuOpen={callbackMenuOpen}
                onMenuClose={callbackMenuClose}
                handleChange={handleChange}
                handleInputChange={handleInputChange}
            />

            {errorText && <div className={cx("error-text")}>{errorText}</div>}

            <Text
                className={cx("address-clinic", {
                    "address-clinic_hide": !isMobileView && isShowClinics,
                })}
            >
                {getAddressByCinic(clinics, selectedClinic)}
            </Text>
        </div>
    );
};
