import { RegionType } from "common/components/PhoneRegionsInput/components/Regions";
import { CountryCode } from "common/constants/enum";

export const getRegion = (regions: RegionType[], code: CountryCode) =>
    regions.find((region) => region.alpha2.toLowerCase() === code);

export const sortRegions = (
    regions: RegionType[],
    currentValue: string | null
) =>
    regions.sort((a) => {
        if (a.phoneCode === currentValue || a.name === currentValue) {
            return -1;
        }

        return 1;
    });
