import {
    OptionsWithGroups,
    OptionProps,
} from "common/components/new/SelectWithSearch";
import { CountryVersion, POPULAR_CITIES } from "common/constants/enum";

export const sortPopularCities = (version: CountryVersion) => (
    options: OptionsWithGroups[]
): OptionsWithGroups[] => {
    const results: OptionsWithGroups = {
        label: "",
        options: [],
    };

    const popularCitiesOptions: OptionProps[] = [];

    options.forEach((item) => {
        item.options.forEach((opt) => {
            if (POPULAR_CITIES[version].includes(opt.value.toLowerCase())) {
                popularCitiesOptions.push(opt);
            }
        });
    });

    popularCitiesOptions.forEach((popularCity) => {
        results.options.push({ ...popularCity, isHide: true });
    });

    return [results, ...options];
};
