import { CSSProperties } from "react";
import { OptionProps, OptionTypeBase, ControlProps } from "react-select";

import { Theme } from "common/constants/enum";

export const customStyles = (
    theme: Theme,
    isMobileView?: boolean,
    isOpenModal?: boolean,
    hasValue?: boolean
) => {
    const isThemeLight = theme === Theme.LIGHT;

    return {
        group: () => ({
            marginBottom: 4,
            position: "relative",
        }),
        option: (
            _: CSSProperties,
            { isSelected }: OptionProps<OptionTypeBase, false>
        ) => {
            return {
                position: "relative",
                paddingTop: 16,
                paddingBottom: 16,
                paddingLeft: 36,
                transition: "0.2s backgroundColor linear",

                ":hover": {
                    backgroundColor: "rgba(238, 243, 244, 0.4)",
                },

                "::after": isSelected && {
                    content: "''",
                    display: "block",
                    position: "absolute",
                    left: isMobileView ? "auto" : 10,
                    right: isMobileView ? 15 : "auto",
                    top: "50%",
                    transform: "translateY(-50%)",
                    backgroundColor: "#424e58",
                    width: 4,
                    height: 4,
                    borderRadius: "50%",
                },
            };
        },
        control: (
            _: CSSProperties,
            state: ControlProps<OptionTypeBase, false>
        ) => ({
            border: "none",
            borderBottom: state.menuIsOpen
                ? "1px solid #45a1f8"
                : isThemeLight
                ? "1px solid rgba(255, 255, 255, 0.2)"
                : "1px solid rgba(66, 78, 88, 0.1)",
            minHeight: "auto",
            height: 56,
            outline: "none",
            boxShadow: "none",
            borderRadius: 0,
            transition: "0.2s borderBottom linear",
            paddingRight: 36,
            paddingLeft: 36,
        }),
        indicatorsContainer: () => ({
            display: "none",
        }),
        noOptionsMessage: (styles: CSSProperties) => ({
            ...styles,
            color: "#424e58",
        }),
        valueContainer: () => ({
            height: 56,
            display: "flex",
            alignItems: "center",
            overflow: "hidden",
        }),
        placeholder: () => ({
            position: "absolute",
            fontWeight: 400,
            fontSize: "17px",
            lineHeight: "24px",
            opacity: 0.6,
            color: isThemeLight && !isMobileView ? "#ffffff" : "#424e58",
        }),
        singleValue: (styles: CSSProperties) => ({
            ...styles,
            fontWeight: "400",
            fontSize: "17px",
            lineHeight: "24px",
            color: isThemeLight && !isMobileView ? "#ffffff" : "#424e58",
            overflow: "hidden",
            opacity: isOpenModal && isMobileView ? 0.6 : 1,
            top: hasValue ? "62%" : "50%",
        }),
        input: () => ({
            position: "relative",
            top: hasValue ? 6 : 0,
            input: {
                fontWeight: "400",
                fontFamily: "inherit",
                fontSize: "17px",
                lineHeight: "24px",
                color: isThemeLight
                    ? "#ffffff !important"
                    : "#424e58 !important",
                maxWidth: 375,
            },
            "input:-webkit-autofill": {
                transition: "background-color 5000s",
                WebkitTextFillColor: isThemeLight
                    ? "#ffffff !important"
                    : "#424e58 !important",
            },
        }),
        menu: (styles: CSSProperties) => ({
            ...styles,
            marginTop: 0,
            zIndex: 300,
            boxShadow: isMobileView
                ? "none"
                : "0px 6px 14px -6px rgba(24, 39, 75, 0.12), 0px 10px 32px -4px rgba(24, 39, 75, 0.1)",
            backgroundColor: "#ffffff",

            "::after": {
                content: "''",
                position: "absolute",
                left: 0,
                bottom: 0,
                width: "100%",
                height: 30,
                backgroundImage: `linear-gradient(
                    180deg,
                    "#ffffff" 0%,
                    "rgba(255, 255, 255, 0)" 100%
                )`,
                transform: "rotate(-180deg)",
            },

            "::before": {
                content: "''",
                position: "absolute",
                left: 0,
                top: 0,
                width: "100%",
                zIndex: 100,
                height: 30,
                backgroundImage: `linear-gradient(
                180deg,
                "#ffffff" 0%,
                "rgba(255, 255, 255, 0)" 100%
            )`,
            },
        }),
        menuList: (styles: CSSProperties) => ({
            ...styles,
            maxHeight: isMobileView ? "75vh" : 300,
            minHeight: isMobileView ? "400px" : "none",
            paddingBottom: 100,
            zIndex: 301,

            "@media(max-width: 280px)": {
                maxHeight: "80vh",
            },
        }),
    };
};
