import { FC } from "react";
import Select, { OptionTypeBase, Styles, GroupTypeBase } from "react-select";
import classnames from "classnames/bind";

import { Theme } from "common/constants/enum";
import { Icon } from "common/components/Icons";
import SearchIcon from "common/components/Icons/icons/24/search.svg";
import DownIcon from "common/components/Icons/icons/24/down.svg";
import CrossIcon from "common/components/Icons/icons/24/cross.svg";

import { customStyles } from "../../customStyles";
import { NoOptionsMessage } from "../../SelectWithSearch.types";
import { SelectWithClinicsProps } from "./SelectWithClinics.types";
import styles from "./SelectWithClinics.module.scss";
import { ClinicList } from "../ClinicList";

const cx = classnames.bind(styles);

export const SelectWithClinics: FC<SelectWithClinicsProps> = ({
    instanceId,
    isMenuOpen,
    isMobileView,
    isOpenModal,
    autoFocus,
    value,
    inputValue,
    placeholder,
    options,
    language,
    theme,
    isShowClinics,
    className,
    clinics,
    selectedClinic,
    filterOption,
    handleChange,
    handleInputChange,
    handleClearInputValue,
    onMenuOpen,
    onMenuClose,
    onChangeClinic,
}) => {
    return (
        <div className={cx("container-select", className)}>
            <Icon
                className={cx("icon-search", {
                    "icon-search_light": theme === Theme.LIGHT,
                })}
            >
                <SearchIcon />
            </Icon>
            <Icon
                className={cx("icon-arrow", {
                    "icon-arrow_active": isMenuOpen,
                    "icon-arrow_light": theme === Theme.LIGHT,
                })}
            >
                <DownIcon />
            </Icon>
            {inputValue || value ? (
                <button
                    type="button"
                    className={cx("button-clear")}
                    onClick={handleClearInputValue}
                >
                    <Icon className={cx("icon-clear")}>
                        <CrossIcon />
                    </Icon>
                </button>
            ) : null}
            {value && placeholder && (
                <label
                    className={cx("placeholder", {
                        placeholder_light: theme === Theme.LIGHT,
                        placeholder_top: Boolean(value),
                    })}
                >
                    {placeholder}
                </label>
            )}
            <Select
                instanceId={instanceId}
                options={options}
                styles={
                    customStyles(
                        theme,
                        isMobileView,
                        isOpenModal,
                        Boolean(value)
                    ) as Styles<
                        OptionTypeBase,
                        false,
                        GroupTypeBase<OptionTypeBase>
                    >
                }
                value={value}
                menuIsOpen={
                    isMobileView && (!value || !isShowClinics)
                        ? true
                        : isMenuOpen
                }
                onChange={handleChange}
                onInputChange={handleInputChange}
                onMenuOpen={onMenuOpen}
                onMenuClose={onMenuClose}
                filterOption={filterOption}
                autoFocus={autoFocus}
                noOptionsMessage={() => NoOptionsMessage[language]}
                blurInputOnSelect={true}
                placeholder={placeholder}
                formatGroupLabel={({ label }) => (
                    <span className={cx("label")}>{label}</span>
                )}
            />
            {isShowClinics && (
                <ClinicList
                    clinics={clinics}
                    language={language}
                    onChange={onChangeClinic}
                    value={selectedClinic}
                />
            )}
        </div>
    );
};
