import { FC } from "react";
import classnames from "classnames/bind";
import { useSwipeable } from "react-swipeable";

import { Button, ButtonTheme } from "common/components/new/Button";

import content from "./content";
import { ModalSheetProps } from "./ModalSheet.types";
import styles from "./ModalSheet.module.scss";

const cx = classnames.bind(styles);

export const ModalSheet: FC<ModalSheetProps> = ({
    children,
    className,
    language,
    isOpen,
    hasValue,
    onClose,
}) => {
    const contentByLanguage = content[language];

    const handlers = useSwipeable({
        onSwipedDown: () => {
            onClose();
        },
    });

    return (
        <div
            className={cx("overlay", {
                overlay_open: isOpen,
            })}
        >
            <div
                className={cx("root", className, {
                    "fade-in-up-big": isOpen,
                })}
            >
                <div {...handlers} className={cx("box")} />

                <div className={cx("content")}>{children}</div>
                <Button
                    className={cx("button-close")}
                    theme={ButtonTheme.TRANSPARENT}
                    type="button"
                    onClick={onClose}
                >
                    {hasValue
                        ? contentByLanguage.textDone
                        : contentByLanguage.textClose}
                </Button>
            </div>
        </div>
    );
};
