import { RefObject } from "react";

import { Language, Theme } from "common/constants/enum";
import { ClinicInfo, ClinicType } from "common/api/location/location.types";

export type OptionProps = {
    value: string;
    label: string;

    isHide?: boolean;
};

export type OptionsWithGroups = {
    label: string;
    options: OptionProps[];
};

export interface SelectWithSearchProps {
    options: OptionProps[];
    clinics: ClinicInfo[];
    isShowClinics: boolean;
    selectedClinic: string;
    onChange: (option: OptionProps) => void;
    onChangeClinic: ({ id, type }: { id: string; type: ClinicType }) => void;

    title?: string;
    theme?: Theme;
    errorText?: string;
    className?: string;
    placeholder?: string;
    disabled?: boolean;
    name?: string;
    value?: OptionProps;
    language?: Language;
    submitRef?: RefObject<HTMLButtonElement>;
    customSortOptions?: (options: OptionsWithGroups[]) => OptionsWithGroups[];
}

export type FilterOption = {
    label: string;
    value: string;
    data: {
        isHide: boolean;
    };
};

