import FingerprintJS from "@fingerprintjs/fingerprintjs-pro";

const FP_TOKEN = process.env.FINGERPRINT_BROWSER_TOKEN;

interface getFingerPrintParams {
    timeout: number;
}

export const getFingerPrintId = async (params: getFingerPrintParams) => {
    if (!FP_TOKEN) return null;

    const fpPromise = FingerprintJS.load({
        token: FP_TOKEN,
    });

    try {
        const fp = await fpPromise;
        const result = await fp.get(params);
        return result.visitorId;
    } catch (error) {
        return null;
    }
};
