import { forwardRef } from "react";
import cn from "classnames/bind";
import InputMask from "react-input-mask";

import { Theme } from "common/constants/enum";

import { InputProps } from "./Input.types";
import styles from "./Input.module.scss";

const cx = cn.bind(styles);

export const Input = forwardRef<HTMLInputElement, InputProps>(
    (
        {
            name,
            placeholder,
            theme = Theme.DARK,
            errorText,
            className,
            classNameInput,
            value = "text",
            type,
            disabled,
            onChange,
            onBlur,
            onFocus,
            onKeyDown,
            mask,
            maxLength,
            tabIndex,
        },
        ref
    ) => {
        const inputClassName = cx("input", classNameInput, {
            input_empty: !value,
            input_error: errorText,
        });

        return (
            <fieldset className={cx("root", className, `theme_${theme}`)}>
                {value && (
                    <label className={cx("label")} htmlFor={name}>
                        {placeholder}
                    </label>
                )}

                {mask ? (
                    <InputMask
                        mask={mask}
                        disabled={disabled}
                        className={inputClassName}
                        type={type}
                        name={name}
                        placeholder={placeholder}
                        value={value === null ? "" : value}
                        onChange={onChange}
                        onBlur={onBlur}
                        onKeyDown={onKeyDown}
                        inputRef={ref}
                        maxLength={maxLength}
                        tabIndex={tabIndex}
                    />
                ) : (
                    <input
                        disabled={disabled}
                        className={inputClassName}
                        type={type}
                        ref={ref}
                        name={name}
                        placeholder={placeholder}
                        value={value === null ? "" : value}
                        onChange={onChange}
                        onBlur={onBlur}
                        onFocus={onFocus}
                        onKeyDown={onKeyDown}
                        tabIndex={tabIndex}
                    />
                )}

                {errorText && (
                    <div className={cx("error-text")}>{errorText}</div>
                )}
            </fieldset>
        );
    }
);
