import { FC, useState, FocusEvent } from "react";
import Downshift from "downshift";
import classnames from "classnames/bind";

import { Input } from "common/components/Input";
import { Icon } from "common/components/Icons";
import DownIcon from "common/components/Icons/icons/24/down.svg";
import { CountryCode } from "common/constants/enum";
import {
    Regions,
    RegionType,
} from "common/components/PhoneRegionsInput/components/Regions";
import { GTMEvents, sendGTMEvent } from "common/utils/metrics";

import { getRegion, sortRegions } from "./helpers";
import { PhoneSelectProps } from "./PhoneSelect.types";
import styles from "./PhoneSelect.module.scss";

const cx = classnames.bind(styles);

export const PhoneSelect: FC<PhoneSelectProps> = ({
    version,
    theme,
    onSwitchRegion,
    regions,
}) => {
    const [isFocused, setIsFocused] = useState(false);
    const [isInputWasChanged, setIsInputWasChanged] = useState(false);

    const handleCountry = (value: RegionType | null) => {
        if (value) {
            onSwitchRegion(value);
            sendGTMEvent(GTMEvents.CHANGE_COUNTRY_CODE_GTM_EVENT);
        }
    };

    const hanldeBlur = (event: FocusEvent<HTMLInputElement>) => {
        event.preventDefault();
        setIsFocused(false);

        if (isInputWasChanged) {
            sendGTMEvent(GTMEvents.CHANGE_INPUT_COUNTRY_CODE_GTM_EVENT);
        }
    };

    const hanldeFocus = () => {
        setIsFocused(true);
    };

    const handleKeyDown = () => {
        setIsInputWasChanged(true);
    };

    const initialRegion = getRegion(regions, CountryCode[version]);

    return (
        <div className={cx("root")}>
            <Downshift
                initialSelectedItem={initialRegion}
                onChange={handleCountry}
                onInputValueChange={() => {
                    setIsFocused(false);
                }}
                itemToString={(item) =>
                    item ? item.phoneCode.replace(/\\/g, "") : ""
                }
            >
                {({
                    getInputProps,
                    getItemProps,
                    getMenuProps,
                    isOpen,
                    inputValue,
                    selectedItem,
                }) => {
                    return (
                        <div>
                            <div
                                style={{
                                    backgroundImage: `url("/images/icons/countries/${
                                        selectedItem
                                            ? selectedItem.alpha2.toLowerCase()
                                            : initialRegion?.alpha2.toLowerCase()
                                    }.svg")`,
                                }}
                                className={cx("icon-country")}
                            />
                            <Input
                                {...getInputProps()}
                                className={cx("input-container")}
                                classNameInput={cx("input")}
                                onBlur={hanldeBlur}
                                onFocus={hanldeFocus}
                                onKeyDown={handleKeyDown}
                                placeholder=""
                                tabIndex={-1}
                            />
                            <Icon className={cx("icon-arrow")}>
                                <DownIcon />
                            </Icon>
                            {(isOpen || isFocused) && (
                                <div
                                    {...getMenuProps()}
                                    className={cx("modal")}
                                >
                                    <Regions
                                        theme={theme}
                                        getItemProps={getItemProps}
                                        regions={
                                            isFocused
                                                ? sortRegions(
                                                      regions,
                                                      inputValue
                                                  )
                                                : regions.filter(
                                                      (item) =>
                                                          !inputValue ||
                                                          item.name
                                                              .toLowerCase()
                                                              .includes(
                                                                  inputValue.toLowerCase()
                                                              ) ||
                                                          item.phoneCode.includes(
                                                              inputValue.toLowerCase()
                                                          )
                                                  )
                                        }
                                    />
                                </div>
                            )}
                        </div>
                    );
                }}
            </Downshift>
        </div>
    );
};
