import { FunctionComponent, useState } from "react";
import { useRouter } from "next/router";
import classnames from "classnames/bind";
import { nanoid } from "nanoid";
import { sha256 } from "js-sha256";

import { useFetchCities } from "common/hooks/useFetchCities";
import { LeadType, postContact, PostVisitorContact } from "common/api/contacts";
import { postFacebookEvent } from "common/api/facebook";
import { getUtmQueryParamsFromCookies } from "common/services/UtmService";
import {
    getOpenPage,
    getSourcePage,
} from "common/services/VisitorTrajectoryService";
import {
    getCookiesValueByName,
    setCookieByObjectValues,
} from "common/utils/cookie";
import {
    COOKIES,
    CountryCode,
    HubspotProperties,
    Theme,
    TwentyFourHoursInSeconds,
} from "common/constants/enum";
import {
    FacebookEvents,
    GTMEvents,
    sendFacebookEvent,
    sendGTMEvent,
    sendSnapchatPixelEvent,
    SnapchatPixelEvents,
} from "common/utils/metrics";
import { getPreferredLanguage } from "common/utils/language";
import { checkIsFromTeenPage } from "common/services/TeensService";
import { redirectWithFormValues } from "common/services/RedirectService";
import {
    getExperimentHubspotValue,
    getExperimentValue,
    PAGE_IN_EXPERIMENT,
} from "common/services/ExperimentService";
import { ClinicType } from "common/api/location";
import { getFingerPrintId } from "common/utils/getFingerPrintId";

import { clearPhone, getCityObj, clearEmail } from "./helpers";
import { FullForm } from "./components/FullForm";
import {
    BookAppointmentFullFormFields,
    BookAppointmentFullFormProps,
} from "./BookAppointmentFullForm.types";
import styles from "./BookAppointmentFullForm.module.scss";

const cx = classnames.bind(styles);
const FINGERPRINT_TIMEOUT = 3000;
const TIME_TO_REDIRECT_ANYWAY = 5000;

export const BookAppointmentFullForm: FunctionComponent<BookAppointmentFullFormProps> = ({
    analyticId,
    className,
    version,
    language,
    additionalContactData = {},
    additionalProperties = {},
    isActionFixed,
    withInstagram,
    defaultCity,
    theme = Theme.DARK,
}) => {
    const router = useRouter();
    const [isLoading, setLoading] = useState(false);
    const citiesByVersion = useFetchCities(version);

    const handleSubmit = async ({
        city: cityName,
        firstName,
        lastName,
        phone,
        email,
        hasSpecialOffers,
        instagramUser,
        clinic,
    }: BookAppointmentFullFormFields) => {
        const cityObj = getCityObj(citiesByVersion, cityName);
        const isTeens = clinic.split("-")[1] === ClinicType.Teens;

        const redirectAnywayPromise = new Promise(() => {
            const wait = setTimeout(() => {
                clearTimeout(wait);
            }, TIME_TO_REDIRECT_ANYWAY);
        });

        setLoading(true);

        const isFromTeensRoute =
            Boolean(router.asPath) && checkIsFromTeenPage(router.asPath);

        const utmQueryParams = getUtmQueryParamsFromCookies();
        const cleanPhone = clearPhone(phone);
        const cleanEmail = clearEmail(email);

        const properties: { [key: string]: unknown } = {};
        const pathname = window?.location?.pathname;
        const openPage = getOpenPage();
        const sourcePage = getSourcePage();
        properties[HubspotProperties.OPEN_PAGE] = openPage;
        properties[HubspotProperties.SUBMIT_PAGE] = pathname;
        properties[HubspotProperties.SOURCE_PAGE] = sourcePage;
        properties[
            HubspotProperties.MARKETING_CONTACT_STATUS
        ] = hasSpecialOffers;

        if (isFromTeensRoute || isTeens) {
            properties[HubspotProperties.LEAD_TYPE] = LeadType.Teens;
        }

        if (instagramUser) {
            properties[HubspotProperties.INSTAGRAM_USER] = instagramUser;
        }

        const hubspotLanguage = getPreferredLanguage(language);

        if (hubspotLanguage) {
            properties[HubspotProperties.LANGUAGE] = hubspotLanguage;
        }

        const isPageInExperiment = PAGE_IN_EXPERIMENT.indexOf(pathname) !== -1;
        if (isPageInExperiment) {
            properties[
                HubspotProperties.EXPERIMENT_ID
            ] = getExperimentHubspotValue();
        }

        const fingerPrintId = await getFingerPrintId({
            timeout: FINGERPRINT_TIMEOUT,
        });

        const clinicId = parseInt(clinic, 10);

        const mainData = {
            firstName,
            lastName,
            email: cleanEmail,
            phone: cleanPhone,
            clinicId,
            ...(fingerPrintId && { fingerprint: fingerPrintId }),
        };

        const dataContact = {
            ...mainData,
            ...additionalContactData,
            properties: {
                ...properties,
                ...additionalProperties,
                ...utmQueryParams,
            },
        } as PostVisitorContact;

        if (cityObj?.id) {
            dataContact.cityId = Number(cityObj.id);
        }

        // Events fire
        const eventID = nanoid();
        const emailToLowerCase = email?.toLowerCase();

        await postFacebookEvent({
            eventName: FacebookEvents.FORM_SUBMIT_FACEBOOK_LEAD_EVENT,
            eventSourceUrl: location.href,
            eventId: eventID,
            userData: {
                country: CountryCode[version],
                city: cityObj?.title,
                firstName,
                lastName,
                email: cleanEmail,
                phone: cleanPhone,
                fbp: decodeURIComponent(getCookiesValueByName(COOKIES.FBP)),
                fbc: decodeURIComponent(getCookiesValueByName(COOKIES.FBC)),
            },
        });

        sendFacebookEvent(
            FacebookEvents.FORM_SUBMIT_FACEBOOK_LEAD_EVENT,
            {
                country: CountryCode[version],
                ...(cityObj && { city: cityObj.title?.toLowerCase() }),
                first_name: firstName.toLowerCase(),
                last_name: lastName.toLowerCase(),
                email: emailToLowerCase,
                phone_number: cleanPhone,
            },
            {
                eventID,
            }
        );

        sendSnapchatPixelEvent(SnapchatPixelEvents.SNAPCHAT_PIXEL_LEAD_EVENT, {
            user_email: emailToLowerCase,
            user_hashed_email: sha256(emailToLowerCase),
            user_phone_number: cleanPhone,
            user_hashed_phone_number: sha256(cleanPhone),
        });

        const experimentValue = getExperimentValue();
        const gtmFormSubmitPayload =
            isPageInExperiment && experimentValue
                ? { experimentId: experimentValue }
                : undefined;
        sendGTMEvent(GTMEvents.FORM_SUBMIT_GTM_EVENT, gtmFormSubmitPayload);

        const postContactPromise = new Promise(async (resolve, _) => {
            await postContact(dataContact);
            resolve("Contact posted!");
        });

        const race = Promise.race([redirectAnywayPromise, postContactPromise]);

        race.then(() => {
            const options = { "max-age": TwentyFourHoursInSeconds, path: "/" };

            const { firstName, lastName, email, phone, clinicId } = mainData;
            setCookieByObjectValues(
                {
                    [COOKIES.FIRST_NAME]: firstName,
                    [COOKIES.LAST_NAME]: lastName,
                    [COOKIES.PHONE]: phone,
                    [COOKIES.EMAIL]: email,
                    [COOKIES.CLINIC_ID]: clinicId,
                    ...(cityObj?.title && { cityName: cityObj?.title }),
                },
                options
            );

            if (clinic) {
                redirectWithFormValues(version, Number(clinic.split("-")[0]));
            }
        }).finally(() => {
            setLoading(false);
        });
    };

    return (
        <div className={cx(className, "root", `root_${theme}`)}>
            {citiesByVersion && citiesByVersion.length > 0 && (
                <FullForm
                    analyticId={analyticId}
                    version={version}
                    language={language}
                    defaultCity={defaultCity}
                    isLoading={isLoading}
                    onFormSubmit={handleSubmit}
                    isActionFixed={isActionFixed}
                    citiesByVersion={citiesByVersion}
                    withInstagram={withInstagram}
                    theme={theme}
                />
            )}
        </div>
    );
};
