import { FC, useState, useEffect, useRef } from "react";
import { useFormikContext } from "formik";
import cn from "classnames/bind";
import countries from "i18n-iso-countries";

import { Input } from "common/components/Input";
import { Theme } from "common/constants/enum";
import phoneCodes from "common/configs/countries/phoneCodes.json";

import { locale } from "./locales";
import { RegionType, RegionTypes } from "./components/Regions";
import { PhoneRegionsInputProps } from "./PhoneRegionsInput.types";
import styles from "./PhoneRegionsInput.module.scss";
import { PhoneSelect } from "../new/PhoneSelect";

const cx = cn.bind(styles);

export const PhoneRegionsInput: FC<PhoneRegionsInputProps> = ({
    name,
    nameCountry,
    placeholder,
    errorText,
    className,
    value = "",
    disabled,
    onChange,
    onBlur,
    mask,
    version,
    language,
    theme = Theme.DARK,
    defaultRegion = RegionTypes.GLOBAL,
    ...rest
}) => {
    const { setFieldValue } = useFormikContext();
    const inputRef = useRef<HTMLInputElement>(null);

    countries.registerLocale(locale[language]);

    const regionsPhones: RegionType[] = Object.keys(
        countries.getAlpha2Codes()
    ).map((alpha2) => ({
        name: countries.getName(alpha2, language),
        alpha2,
        phoneCode: `+${phoneCodes[alpha2 as keyof typeof phoneCodes]}`,
    }));

    const regionByDefault = regionsPhones.find(
        (region) => region.phoneCode === defaultRegion
    );
    const [currentArrowContent, setArrowContent] = useState<
        RegionType | undefined
    >(regionByDefault);

    const handleSwitchRegion = (region: RegionType) => {
        setArrowContent(region);

        if (inputRef.current) {
            inputRef.current.focus();
        }
    };

    useEffect(() => {
        if (name && currentArrowContent) {
            setFieldValue(
                nameCountry,
                currentArrowContent.phoneCode.replace(/\\/g, "")
            );
        }
    }, [currentArrowContent]);

    return (
        <div className={cx(className, "root", `theme_${theme}`)}>
            <PhoneSelect
                version={version}
                regions={regionsPhones}
                theme={theme}
                onSwitchRegion={handleSwitchRegion}
            />

            <div className={cx("fieldset")}>
                {value && (
                    <label className={cx("label")} htmlFor={name}>
                        {placeholder}
                    </label>
                )}

                <Input
                    placeholder={placeholder}
                    name={name}
                    ref={inputRef}
                    value={value === null ? "" : value}
                    type="tel"
                    onBlur={onBlur}
                    onChange={onChange}
                    theme={theme}
                    disabled={disabled}
                    className={cx("input")}
                    errorText={errorText}
                    {...rest}
                />
            </div>
        </div>
    );
};
