import React, { FunctionComponent } from "react";
import cn from "classnames/bind";

import { Theme } from "common/constants/enum";

import { CheckboxProps } from "./Checkbox.types";
import styles from "./Checkbox.module.scss";

const cx = cn.bind(styles);

export const Checkbox: FunctionComponent<CheckboxProps> = (props) => {
    const {
        name,
        placeholder,
        errorText,
        className,
        checked = false,
        disabled,
        onChange,
        theme = Theme.DARK,
        ...rest
    } = props;
    const inputClassName = cx("input", errorText && "inputError");

    return (
        <fieldset className={cx(className, "root", `theme_${theme}`)}>
            <label
                className={cx("label", {
                    label_light: theme === Theme.LIGHT,
                })}
            >
                <input
                    type={"checkbox"}
                    disabled={disabled}
                    className={inputClassName}
                    name={name}
                    checked={checked}
                    onChange={onChange}
                    {...rest}
                />

                {placeholder && (
                    <span className={cx("placeholder")}>{placeholder}</span>
                )}
            </label>

            {errorText && <div className={cx("error-text")}>{errorText}</div>}
        </fieldset>
    );
};
