import { COOKIES, ThirtyMinutesInSeconds } from "common/constants/enum";
import { getCookiesValueByName, setCookie } from "common/utils/cookie";
import { GTMEvents, sendGTMEvent } from "common/utils/metrics";

import {
    PAGE_IN_EXPERIMENT,
    GroupType,
    GroupHubspotValueType,
    ExperimentSetUpType,
} from "./constants";

export const runExperiment = (pathname: string) => {
    const checkIsExperimentSetUp = getExperimentSetUpCookie();
    const isPageInExperiment = PAGE_IN_EXPERIMENT.indexOf(pathname) !== -1;
    const experimentValue = getExperimentValue();

    if (checkIsExperimentSetUp) {
        return;
    }

    if (isPageInExperiment && experimentValue) {
        sendGTMEvent(GTMEvents.UNIVERSAL_LANDING_IN_EXPERIMENT_GTM_EVENT, {
            experimentId: experimentValue,
        });

        setExperimentSetUpCookie(ExperimentSetUpType.EXPERIMENT_SET_UP_ON);
    }
};

export const getExperimentValue = () => {
    const experimentId = getCookiesValueByName(COOKIES.EXPERIMENTS);

    if (experimentId === GroupType.CONTROL_GROUP) {
        return GroupType.CONTROL_GROUP;
    }

    if (experimentId === GroupType.EXPERIMENT_GROUP) {
        return GroupType.EXPERIMENT_GROUP;
    }

    return null;
};

export const getExperimentHubspotValue = () => {
    const experimentId = getCookiesValueByName(COOKIES.EXPERIMENTS);

    if (experimentId === GroupType.CONTROL_GROUP) {
        return GroupHubspotValueType.CONTROL_GROUP;
    }

    if (experimentId === GroupType.EXPERIMENT_GROUP) {
        return GroupHubspotValueType.EXPERIMENT_GROUP;
    }
};

export const getExperimentSetUpCookie = (): boolean => {
    const isExperimentSetUp = getCookiesValueByName(
        COOKIES.IS_EXPERIMENT_SET_UP
    );

    return isExperimentSetUp === ExperimentSetUpType.EXPERIMENT_SET_UP_ON;
};

export const setExperimentSetUpCookie = (value: ExperimentSetUpType) => {
    setCookie(COOKIES.IS_EXPERIMENT_SET_UP, value, {
        path: "/",
        "max-age": ThirtyMinutesInSeconds,
    });
};
