import { FC, useRef, useEffect, useState } from "react";
import { useRouter } from "next/router";
import classnames from "classnames/bind";
import { Form, Formik } from "formik";
import useTranslation from "next-translate/useTranslation";

import { CountryPhoneCode, Theme } from "common/constants/enum";
import { convertQueryStringToQueryObject } from "common/utils/queryString";
import { sortPopularCities } from "common/utils/sortPopularCities";
import { Input } from "common/components/Input";
import { PhoneRegionsInput } from "common/components/PhoneRegionsInput";
import { Link } from "common/components/Link";
import { Checkbox } from "common/components/Checkbox";
import { Button, ButtonSize } from "common/components/new/Button";
import { SelectWithSearch } from "common/components/new/SelectWithSearch";
import { isHasCombinedClinics } from "common/components/new/SelectWithSearch/helpers/isHasCombinedClinics";
import { checkIsFromTeenPage } from "common/services/TeensService";
import { ClinicInfo, ClinicType } from "common/api/location";

import content from "../../content";
import { getValidationSchema } from "../../validation";
import {
    clearPhone,
    convertCitiesToOptions,
    getButtonTheme,
    getCheckboxTheme,
    getCityOption,
    getClinicsByCity,
} from "../../helpers";
import {
    BookAppointmentFullFormFields,
    BookingFormContentType,
} from "../../BookAppointmentFullForm.types";
import { FullFormProps } from "./FullForm.types";
import styles from "./FullForm.module.scss";

const cx = classnames.bind(styles);

export const FullForm: FC<FullFormProps> = ({
    analyticId,
    version,
    language,
    isLoading,
    onFormSubmit,
    isActionFixed,
    citiesByVersion,
    withInstagram,
    defaultCity,
    theme = Theme.DARK,
}) => {
    const { t } = useTranslation("common-form");
    const submitRef = useRef<HTMLButtonElement>(null);
    const router = useRouter();
    const [
        isOnceTeenClinicFromTeenPageState,
        setIsOnceTeenClinicFromTeenPageState,
    ] = useState(false);
    const contentByVersion = content[language](t, { version });

    const citiesOptions = convertCitiesToOptions(citiesByVersion);
    const isFromTeensRoute =
        Boolean(router.asPath) && checkIsFromTeenPage(router.asPath);

    const searchQueryObject =
        typeof window !== "undefined"
            ? convertQueryStringToQueryObject(window?.location?.search)
            : {};

    const cityObj = getCityOption(
        citiesOptions,
        defaultCity || searchQueryObject?.location
    );

    const bookForm: BookAppointmentFullFormFields = {
        city: cityObj?.value || "",
        clinic: "",
        firstName: "",
        lastName: "",
        phone: "",
        country: CountryPhoneCode[version],
        email: "",
        hasSpecialOffers: false,
        isAccept: false,
        ...(withInstagram && { instagramUser: "" }),
    };

    const firstNameRef = useRef<HTMLInputElement>(null);

    const {
        cityPlaceholder,
        firstNamePlaceholder,
        lastNamePlaceholder,
        phoneNumberPlaceholder,
        emailPlaceholder,
        checkboxSpecialOffersPlaceholder,
        checkboxAcceptPlaceholder,
        checkboxAcceptInstagramPlaceholder,
        action,
        instagramUserPlaceholder,
        titleModal,
    } = contentByVersion as BookingFormContentType;

    const handleChangeClinic = (
        onChange: (
            field: string,
            value: any,
            shouldValidate?: boolean | undefined
        ) => void
    ) => ({ id, type }: { id: string; type: ClinicType }) => {
        onChange(
            "clinic",
            type === ClinicType.Teens && id.split("-")[1] !== ClinicType.Teens
                ? `${id}-teens`
                : id
        );
    };

    const handleSubmit = async ({
        city,
        country,
        firstName,
        lastName,
        phone,
        email,
        hasSpecialOffers,
        instagramUser,
        clinic,
    }: BookAppointmentFullFormFields) => {
        const cleanPhone = clearPhone(country + phone);

        const dataContact = {
            city,
            firstName: firstName.trim(),
            lastName: lastName.trim(),
            phone: cleanPhone,
            email: email.trim(),
            hasSpecialOffers,
            clinic,
            instagramUser: instagramUser?.trim(),
        } as BookAppointmentFullFormFields;

        onFormSubmit(dataContact);
    };

    const isThemeLight = theme === Theme.LIGHT;

    const buttonTheme = getButtonTheme(theme, version);
    const checkboxTheme = getCheckboxTheme(theme, version);

    return (
        <Formik
            validationSchema={getValidationSchema({
                ...contentByVersion,
                withInstagram,
            })}
            initialValues={bookForm}
            onSubmit={handleSubmit}
        >
            {({
                handleSubmit,
                handleBlur,
                handleChange,
                setFieldValue,
                touched,
                errors,
                values,
            }) => {
                const clinics = getClinicsByCity(citiesByVersion, values.city);
                let clinicsForList: ClinicInfo[] | [] = clinics;

                useEffect(() => {
                    const clinics = getClinicsByCity(
                        citiesByVersion,
                        values.city
                    );

                    const currentClinic = clinics.find(
                        ({ id }) =>
                            id === parseInt(searchQueryObject?.clinicId, 10)
                    );

                    if (isOnceTeenClinicFromTeenPageState) {
                        setFieldValue(
                            "clinic",
                            `${currentClinic?.id || clinics[0].id}-teens`
                        );
                    } else if (clinics.length >= 1) {
                        setFieldValue(
                            "clinic",
                            `${currentClinic?.id || clinics[0].id}`
                        );
                    }
                }, [values.city, isOnceTeenClinicFromTeenPageState]);

                if (isFromTeensRoute) {
                    clinicsForList = clinics.filter(
                        (clinic) => clinic.clinicType !== ClinicType.Adults
                    );
                }

                const isOnceTeenClinicFromTeenPage =
                    clinicsForList.length === 1 &&
                    clinicsForList[0].clinicType === ClinicType.Combined &&
                    isFromTeensRoute;

                const isShowClinics =
                    clinicsForList &&
                    !isOnceTeenClinicFromTeenPage &&
                    (clinicsForList?.length > 1 ||
                        isHasCombinedClinics(clinicsForList));

                const onChangeClinic = handleChangeClinic(setFieldValue);
                setIsOnceTeenClinicFromTeenPageState(
                    isOnceTeenClinicFromTeenPage
                );

                return (
                    <Form onSubmit={handleSubmit}>
                        <Input
                            placeholder={firstNamePlaceholder}
                            name="firstName"
                            key="firstName"
                            ref={firstNameRef}
                            value={values.firstName}
                            onBlur={handleBlur}
                            onChange={handleChange}
                            theme={theme}
                            errorText={
                                touched.firstName && errors.firstName
                                    ? errors.firstName
                                    : undefined
                            }
                        />

                        <Input
                            placeholder={lastNamePlaceholder}
                            name="lastName"
                            key="lastName"
                            value={values.lastName}
                            onBlur={handleBlur}
                            onChange={handleChange}
                            theme={theme}
                            errorText={
                                touched.lastName && errors.lastName
                                    ? errors.lastName
                                    : undefined
                            }
                        />

                        {withInstagram && (
                            <Input
                                placeholder={instagramUserPlaceholder}
                                name="instagramUser"
                                key="instagramUser"
                                value={values.instagramUser || ""}
                                onBlur={handleBlur}
                                onChange={handleChange}
                                theme={theme}
                                errorText={
                                    touched.instagramUser &&
                                    errors.instagramUser
                                        ? errors.instagramUser
                                        : undefined
                                }
                            />
                        )}

                        <Input
                            placeholder={emailPlaceholder}
                            name="email"
                            key="email"
                            type="email"
                            value={values.email}
                            onBlur={handleBlur}
                            onChange={handleChange}
                            theme={theme}
                            errorText={
                                touched.email && errors.email
                                    ? errors.email
                                    : undefined
                            }
                        />

                        <PhoneRegionsInput
                            defaultRegion={CountryPhoneCode[version]}
                            placeholder={phoneNumberPlaceholder}
                            name="phone"
                            nameCountry="country"
                            version={version}
                            language={language}
                            value={values.phone}
                            onBlur={handleBlur}
                            onChange={handleChange}
                            theme={theme}
                            errorText={
                                touched.phone && errors.phone
                                    ? errors.phone
                                    : undefined
                            }
                        />

                        <SelectWithSearch
                            placeholder={cityPlaceholder}
                            onChangeClinic={onChangeClinic}
                            selectedClinic={values.clinic}
                            className={cx("select-cities")}
                            options={citiesOptions}
                            onChange={({ value }) => {
                                setFieldValue("city", value);
                            }}
                            theme={theme}
                            title={titleModal}
                            submitRef={submitRef}
                            isShowClinics={isShowClinics}
                            clinics={clinics}
                            customSortOptions={sortPopularCities(version)}
                            value={getCityOption(citiesOptions, values.city)}
                            language={language}
                            name="city"
                            key="city"
                            errorText={
                                touched.city && errors.city
                                    ? errors.city
                                    : undefined
                            }
                        />

                        <Checkbox
                            placeholder={checkboxSpecialOffersPlaceholder}
                            name="hasSpecialOffers"
                            checked={values.hasSpecialOffers}
                            onBlur={handleBlur}
                            onChange={handleChange}
                            theme={checkboxTheme}
                            errorText={
                                touched.hasSpecialOffers &&
                                errors.hasSpecialOffers
                                    ? errors.hasSpecialOffers
                                    : undefined
                            }
                        />

                        <Checkbox
                            placeholder={
                                withInstagram ? (
                                    <>
                                        {
                                            checkboxAcceptInstagramPlaceholder.text
                                        }
                                        &nbsp;
                                        <Link
                                            target={"_blank"}
                                            link={
                                                checkboxAcceptInstagramPlaceholder.link
                                            }
                                        >
                                            {
                                                checkboxAcceptInstagramPlaceholder.name
                                            }
                                        </Link>
                                    </>
                                ) : (
                                    <>
                                        {checkboxAcceptPlaceholder.text}&nbsp;
                                        <Link
                                            target={"_blank"}
                                            link={
                                                checkboxAcceptPlaceholder.link
                                            }
                                        >
                                            {checkboxAcceptPlaceholder.name}
                                        </Link>
                                    </>
                                )
                            }
                            name="isAccept"
                            checked={values.isAccept}
                            onBlur={handleBlur}
                            onChange={handleChange}
                            theme={checkboxTheme}
                            errorText={
                                touched.isAccept && errors.isAccept
                                    ? errors.isAccept
                                    : undefined
                            }
                            className={cx("checkbox-is-accept")}
                        />

                        <div
                            className={cx("mobile-action-wrapper", {
                                "mobile-action-wrapper_is-fixed": isActionFixed,
                            })}
                        >
                            <Button
                                type="submit"
                                id={analyticId}
                                isLoading={isLoading}
                                size={ButtonSize.M}
                                ref={submitRef}
                                theme={buttonTheme}
                                className={cx("action", {
                                    "action_is-theme-light": isThemeLight,
                                })}
                            >
                                {action}
                            </Button>
                        </div>
                    </Form>
                );
            }}
        </Formik>
    );
};
